




























































import Vue from 'vue';
import { peopleModule } from '../store/modules/people';
import Person from '../data/person';
import { configModule } from '../store/modules/config';

export default Vue.extend({
  name: 'Report',
  data() {
    return {
      valid: false as boolean,
      includeDetails: false as boolean,
      responseMessage: '' as string,
      responseColor: 'primary' as string,
      loading: false as boolean,
      subject: '' as string,
      subjectRules: [
        (v: string) => !!v || 'Issue subject is required',
        (v: string) => (v && v.length <= 200) || 'Issue subject must be less than 200 characters',
      ],
      messageRules: [
        (v: string) => !!v || 'Issue subject is required',
        (v: string) => (v && v.length <= 2000) || 'Max 2000 characters'
        ],
      message: "" as string
    }
  },
  computed: {
    selfPerson(): Person | undefined {
      return peopleModule.selfPerson;
    },
    recentSubmission(): boolean {
      return configModule.recentReport;
    }
  },
  methods: {
    confirm (): void {
      this.loading = true;
      const params = {
        subject: this.subject,
        message: this.message
      } as any;
      if (this.includeDetails && this.selfPerson) {
        params.email = this.selfPerson.email;
        params.name = this.selfPerson.name;
      }
      const json: string = JSON.stringify(params);
      this.sendRequest(json)
        .then(response => {
          configModule.setLastReport();
          this.responseColor = 'success';
          this.responseMessage = response;
          this.loading = false;
        })
        .catch(err => {
          this.loading = false;
          this.responseColor = 'error';
          this.responseMessage = err;
        })
    },
    sendRequest(json: string): Promise<string> {
      return new Promise((resolve, reject) => {
        const xhttp = new XMLHttpRequest();
        xhttp.onreadystatechange = function(this: XMLHttpRequest) {
          if (this.readyState == 4) {
            if (this.status == 200) {
              resolve('Issue submitted.');
            } else {
              reject('Something went wrong. Please try again later.');
            }
          }
        }
        xhttp.open("POST", "help.php", true);
        xhttp.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        xhttp.send(json);
      });
    }
  }
});
